<template>
  <div id="app">
    <h1>My To-Do List</h1>
    <to-do-form @todo-added="addToDo"></to-do-form>
    <h2 id="list-summary" ref="listSummary" tabindex="-1">{{listSummary}}</h2>
    <ul aria-labelledby="list-summary" class="stack-large">
      <li v-for="item in ToDoItems" :key="item.id">
        <to-do-item
          :label="item.label"
          :done="item.done"
          :id="item.id"
          @checkbox-changed="updateDoneStatus(item.id)"
          @item-deleted="deleteToDo(item.id)"
          @item-edited="editToDo(item.id, $event)">
        </to-do-item>
      </li>
    </ul>
    <el-pagination
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :current-page="currentPage"
      :page-sizes="[5, 10, 20, 50]"
      :page-size="pageSize"
      layout="total, sizes, prev, pager, next, jumper"
      :total="ToDoItems.length">
    </el-pagination>
    <div id="main" style="width: 600px;height:400px;"></div>
  </div>
</template>


<script>
import ToDoItem from "./components/ToDoItem.vue";
import uniqueId from "lodash.uniqueid";
import ToDoForm from "./components/ToDoForm.vue";
import * as echarts from 'echarts';

export default {
  name: "app",
  components: {
    ToDoItem,
    ToDoForm,
  },
  data() {
    return {
      ToDoItems: [
        { id: uniqueId("todo-"), label: "Learn Vue", done: false },
        { id: uniqueId("todo-"), label: "Create a Vue project with the CLI", done: true },
        { id: uniqueId("todo-"), label: "Have fun", done: true },
        { id: uniqueId("todo-"), label: "Create a to-do list", done: false },
      ],
      currentPage: 1,
    pageSize: 10,
    myChart: null,
    };
  },
  methods: {
    addToDo(toDoLabel) {
    this.ToDoItems.push({ id: uniqueId('todo-'), label: toDoLabel, done: false });
    this.updateChart();
  },
  deleteToDo(toDoId) {
    const itemIndex = this.ToDoItems.findIndex((item) => item.id === toDoId);
    this.ToDoItems.splice(itemIndex, 1);
    this.updateChart();
  },
  updateDoneStatus(toDoId) {
    const toDoToUpdate = this.ToDoItems.find((item) => item.id === toDoId);
    toDoToUpdate.done = !toDoToUpdate.done;
    this.updateChart();
  },
editToDo(toDoId, newLabel) {
  const toDoToEdit = this.ToDoItems.find((item) => item.id === toDoId);
  toDoToEdit.label = newLabel;
},
handleSizeChange(val) {
    this.pageSize = val;
  },
  handleCurrentChange(val) {
    this.currentPage = val;
  },
  initChart() {
      this.myChart = echarts.init(document.getElementById('main'));
      this.updateChart();
    },
    updateChart() {
    const doneCount = this.ToDoItems.filter(item => item.done).length;
    const totalCount = this.ToDoItems.length;
    const doneRatio = totalCount > 0 ? doneCount / totalCount : 0;

    const option = {
      title: {
        text: 'To-Do List Summary',
        left: 'center',
        textStyle: {
          color: '#333',
          fontSize: 18,
          fontWeight: 'bold',
        },
      },
      tooltip: {
        trigger: 'item',
        formatter: '{a} <br/>{b}: {c} ({d}%)',
        // 自定义提示框的样式
        backgroundColor: '#fff',
        borderColor: '#ccc',
        borderWidth: 1,
        padding: 10,
        textStyle: {
          color: '#333',
        },
      },
       // 配置图例，点击图例可以切换显示/隐藏
       legend: {
        orient: 'vertical',
        left: 'left',
        data: ['Done', 'Not Done'],
        // 自定义图例的样式
        textStyle: {
          color: '#333',
        },
      },
      series: [
        {
          name: 'To-Do Items',
          type: 'pie',
          radius: '55%',
          center: ['50%', '60%'],
          data: [
            { value: doneRatio, name: 'Done' },
            { value: 1 - doneRatio, name: 'Not Done' },
          ],
          // 自定义系列的样式
          itemStyle: {
            emphasis: {
              shadowBlur: 10,
              shadowOffsetX: 0,
              shadowColor: 'rgba(0, 0, 0, 0.5)',
            },
          },
        },
      ],
      toolbox: {
        show: true,
        feature: {
          saveAsImage: {
            show: true,
            title: 'Save as Image',
            iconStyle: {
              borderColor: '#FFA500', // 自定义按钮边框颜色
              borderWidth: 2,
            },
            emphasis: {
              iconStyle: {
                borderColor: '#FF6347', // 自定义按钮边框颜色（鼠标悬停时）
              },
            },
          },
        },
      },
    };

    // 使用设置好的选项绘制图表
    this.myChart.setOption(option);
  },
  },
  computed: {
    listSummary() {
      const numberFinishedItems = this.ToDoItems.filter((item) => item.done).length;
      return `${numberFinishedItems} out of ${this.ToDoItems.length} items completed`;
    },
    paginatedToDoItems() {
    const start = (this.currentPage - 1) * this.pageSize;
    const end = this.currentPage * this.pageSize;
    return this.ToDoItems.slice(start, end);
  },
  doneRatio() {
    const doneCount = this.ToDoItems.filter(item => item.done).length;
    const totalCount = this.ToDoItems.length;
    return totalCount > 0 ? doneCount / totalCount : 0;
  },
  },
 
  mounted() {
    // 初始化ECharts实例
    const chartDom = document.getElementById('main');
    const myChart = echarts.init(chartDom);
    this.initChart();
    // 设置图表选项
    const option = {
      title: {
        text: 'To-Do List Summary',
      },
      tooltip: {},
      legend: {
        data: ['Done', 'Not Done'],
      },
      xAxis: {
        data: ['Done', 'Not Done'],
      },
      yAxis: {},
      series: [
        {
          name: 'To-Do Items',
          type: 'bar',
          data: [this.doneRatio, 1 - this.doneRatio],
        },
      ],
    };

    // 使用设置好的选项绘制图表
    myChart.setOption(option);
    this.myChart = echarts.init(document.getElementById('main'));
  this.updateChart();
  },

  watch: {
  ToDoItems: {
    deep: true,
    handler(newVal) {
      // 使用newVal参数进行一些操作
      console.log('To-Do Items updated:', newVal);

      // 更新ECharts图表
      this.updateChart();
    },
  },
},


}
</script>


